section {
    padding-top: 1rem;
}

.about_container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about_me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about_me-img {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about_me-img:hover {
    transform: rotate(0deg);
}

.about_cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5em;
}

.about_card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}

.about_card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.about_icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about_card h5 {
    font-size: 0.95rem;
}

.about_card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about_content p {
    margin: 1rem 0 1rem;
}

/*================ MEDIA QUERIES (MEDIUM DEVICES) ================*/
@media screen and (max-width: 1024px){
    .about_container{
        grid-template-columns: 1fr;
        gap: 0;
        padding: 0rem;
       }

    .about_me{
        width: 0;
        margin: 0rem auto 0rem;
       }

    .about_content p {
        margin: 2rem 0 2rem;
        text-align: justify;
       }

    .about_cards {
        display: grid;
        justify-content: center;
        grid-template-columns: repeat(3, 1fr);
        grid-template-columns: 0.5fr;
        gap: 1em;
    }
  }
  /*================ MEDIA QUERIES(SMALL DEVICES)=================*/
  @media screen and (max-width: 600px) {
    section {
        height: 150vh !important;
        width: 65vh;
        padding-top: 0.5rem;
    }
    section > h2 {
        padding: 0rem !important;
        margin-bottom: 1.5rem !important;
    }
    .about_me-img {
        border-radius: 2rem;
        overflow: hidden;
        transform: rotate(10deg);
        transition: var(--transition);
    }
        .about_me {
          width: 45%;
          margin: 0 auto 2rem;
       }
        .about_card {
            grid-template-columns: repeat(3, 1fr);
          grid-template-columns: 1fr 1fr;
          gap: 1rem;
       }
       .about_cards {
        grid-template-columns: repeat(3, 1fr);
      grid-template-columns: 1fr 1fr;
      gap: 1rem;
   }
        .about_content {
          text-align: center;
       }
    .about_content p {
        margin: 1.5rem 0;
        margin-bottom: 1.5rem;
    }
}