.services_container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
    padding-top: 2rem;
}

.service_head {
    background: var(--color-primary);
    text-align: center;
    padding: 1.2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}

.service_head > h3 {
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.service {
    background: var(--color-bg-variant);
    border-bottom-right-radius: 2rem;
    border-bottom-left-radius: 2rem;
    border: 1px solid var(--color-primary);
    height: fit-content;
    transition: var(--transition);
}

.service:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.service_list {
    padding: 2rem;
}

.service_list li {
    display: flex;
    gap: 1rem;
    margin-bottom: 0.8rem;
}

.service_list-icon {
    color: var(--color-primary);
    margin-top: 2px;
}

.service_list p {
    font-size: 0.9rem;
}

/*================ MEDIA QUERIES (MEDIUM DEVICES) ================*/
@media screen and (max-width: 1024px) {
    .services_container{
        grid-template-columns: 1fr 1fr;
        gap: 3rem;
        row-gap: 4rem;
       }
    .service{
        height: auto;
       }
}
  /*================ MEDIA QUERIES(SMALL DEVICES)=================*/
  @media screen and (max-width: 600px) {
    #services {
        height: 135vh !important;
        width: 65vh;
        padding-top: 0.5rem;
    }
    .services_container {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 2rem;
        padding-top: 0rem;
    }
    .services_container {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
    .service_head {
        padding: 0.5rem;
    }
    .service_list {
        padding: 1rem;
        padding-left: 3rem;
    }
    .service_list li {
        display: flex;
        gap: 1rem;
        margin-bottom: 0.8rem;
    }
}