footer {
    background: var(--color-primary);
    padding: 1rem 0rem 7rem 0rem;
    text-align: center;
    font-size: 0.9rem;
}

footer a {
    color: var(--color-bg);
}

.footer_logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 0rem;
    display: block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 1rem;
}

.footer_socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 1rem;
}

.footer_socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer_socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

/*================ MEDIA QUERIES(SMALL DEVICES)=================*/
@media screen and (max-width: 600px) {
    footer {
        height: 68vh !important;
        width: 65vh;
        padding-top: 0.5rem;
        padding-bottom: 0rem;
    }
    .permalinks {
        flex-direction: column;
        gap: .5rem;
    }

    .footer_socials {
        margin-bottom: 0.5rem;
    }
}